import React from 'react';
import {Link} from 'react-router-dom'
import Layout from '../../components/Layout'
import { Col } from 'reactstrap';
import styles from '../../css/Home.css'
import {isAuth} from '../../auth/window'

function Home() {
    return (
        <Layout>
            <Col md="9" className="back1">
            <div className="imageText">
                <h1 className="animate__animated animate__zoomInDown animate__slow homeh1">Welcome to the quizee</h1>
                <br/>
                <h6 className="homeh6">A Platform to Evaluate.</h6>
                <br/> 
                <br/> 
                {!isAuth() ?<Link to="/signin"><button className="homebtn homebtn1">Sign In</button></Link> 
                :
                <Link to="/about"><button className="homebtn homebtn1">About</button></Link>
                }
                {!isAuth() ?
                <Link to="/signup"><button className="homebtn homebtn2">Sign Up</button></Link> 
                :
                <Link to="/contact"><button className="homebtn homebtn2">Contact Us</button></Link> 
                }
            </div>
            </Col>
        </Layout>   
    )
  }
  
  export default Home;